@import "../../../scss/variables";
@import "../../../scss/mixins";

.favorites {
    padding: 72px 0;

    .favorites__link {
        margin-bottom: 32px;

        a {
            opacity: .7;
            transition: .2s;
    
            &:hover {
                opacity: 1;
            }
        }
    } 

    h1 {
        @include h1;
        margin-bottom: 48px;
    }

    .empty {
        font-size: 40px;
        font-family: "Helios";
        color: $_contextual-color-red;
        text-align: center;
        margin-bottom: 60px;
    }
}