@import "../../../scss/variables";
@import "../../../scss/mixins";

.cart-book {
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid $_system-color-secondary;
    margin-bottom: 48px;

    .book-card__image {
        width: 256px;
        overflow: hidden;
        margin: 0 auto 30px;
        background-color: $_tertiary-color-blue;
        display: flex;
        justify-content: center;
        align-items: center;
        
        img {
            object-fit: cover;
            object-position: center;
            width: 226px;
            height: 264px;
        }
    }

    .cart-book__main {
        flex-grow: 1;
        padding: 24px 32px;

        h3 {
            @include h3;
            margin-bottom: 8px;
        }

        .cart-book__subtitle {
            font-size: 16px;
            font-family: "Helios";
            color: $_system-color-secondary;
            margin-bottom: 24px;
        }

        .cart-book__main-count {
            display: flex;
            justify-content: space-evenly;
            align-items: center;
            width: 168px;
            font-size: 24px;

            button {
                background-color: inherit;
                border: none;
                outline: none;
                font-size: 36px;
                line-height: 1;
                @include button;
            }
        }
    }

    .cart-book__price {
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 40px;
        font-family: "Bebas Neue";
        line-height: 1;
        width: 20%;
    }

    .cart-book__delete {
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 0 20px;

        button {
            background-color: inherit;
            border: none;
            outline: none;
            font-size: 24px;
            line-height: 1;
            @include button;
        }
    }
}